export default function Card({ card, onClick }) {
  return (
    <div
      className={`w-24 h-24 rounded-lg cursor-pointer flex items-center justify-center text-3xl border-2 border-gray-500 duration-500 transform ${
        card.flipped ? "bg-green-500 rotate-y-180" : "bg-gray-700"
      }`}
      onClick={() => !card.flipped && onClick(card)}
    >
      {card.flipped ? card.emoji : "‚ùì"}
    </div>
  );
}
