import { useState, useEffect } from "react";
import GameBoard from "../components/GameBoard";

const cardEmojis = ["üçé", "üçå", "üçá", "üçì", "üçí", "ü•ù", "üçç", "üçë"];

export default function Home() {
  const [cards, setCards] = useState([]);
  const [firstCard, setFirstCard] = useState(null);
  const [secondCard, setSecondCard] = useState(null);
  const [disabled, setDisabled] = useState(false);
  const [moves, setMoves] = useState(0);
  const [time, setTime] = useState(0);
  const [gameOver, setGameOver] = useState(false);

  const startGame = () => {
    const shuffledCards = [...cardEmojis, ...cardEmojis]
      .sort(() => 0.5 - Math.random())
      .map((emoji, i) => ({
        id: i,
        emoji,
        flipped: false,
        matched: false,
      }));

    setCards(shuffledCards);
    setFirstCard(null);
    setSecondCard(null);
    setDisabled(false);
    setMoves(0);
    setTime(0);
    setGameOver(false);
  };

  useEffect(() => {
    startGame();
  }, []);

  useEffect(() => {
    let timer;
    if (!gameOver) {
      timer = setInterval(() => setTime((prev) => prev + 1), 1000);
    }
    return () => clearInterval(timer);
  }, [gameOver]);

  const handleCardClick = (card) => {
    if (disabled || card.flipped) return;

    const updatedCards = [...cards];
    updatedCards[card.id].flipped = true;
    setCards(updatedCards);

    if (!firstCard) {
      setFirstCard(card);
    } else {
      setSecondCard(card);
      setDisabled(true);
      setMoves((m) => m + 1);

      if (firstCard.emoji === card.emoji) {
        updatedCards[firstCard.id].matched = true;
        updatedCards[card.id].matched = true;
        setCards(updatedCards);

        resetTurn();

        if (updatedCards.every((c) => c.matched)) {
          setGameOver(true);
        }
      } else {
        setTimeout(() => {
          updatedCards[firstCard.id].flipped = false;
          updatedCards[card.id].flipped = false;
          setCards(updatedCards);
          resetTurn();
        }, 800);
      }
    }
  };

  const resetTurn = () => {
    setFirstCard(null);
    setSecondCard(null);
    setDisabled(false);
  };

  return (
    <div className="min-h-screen flex flex-col items-center p-6">
      <h1 className="text-4xl font-bold mb-4">Memory Card Game üéÆ</h1>

      <div className="flex gap-6 text-xl font-semibold">
        <p>Moves: {moves}</p>
        <p>Time: {time}s</p>
      </div>

      <GameBoard cards={cards} onCardClick={handleCardClick} />

      <button
        onClick={startGame}
        className="mt-6 px-6 py-2 bg-blue-600 rounded-lg hover:bg-blue-700 transition"
      >
        üîÑ Restart Game
      </button>

      {gameOver && (
        <div className="mt-6 flex flex-col items-center gap-4">
          <p className="text-green-400 font-bold text-2xl">
            üéâ You Won in {moves} moves! ‚è± {time}s
          </p>

          <button
            className="px-6 py-2 bg-yellow-600 rounded-lg hover:bg-yellow-700"
            onClick={() => {
              const name = prompt("Enter your name:");
              if (name) {
                import("../lib/api")
                  .then(({ submitScore }) =>
                    submitScore(name, moves, time)
                  )
                  .then(() => alert("Score submitted ‚úÖ"))
                  .catch(() => alert("Submission failed ‚ùå"));
              }
            }}
          >
            Save Score üìù
          </button>

          <a
            href="/leaderboard"
            className="px-6 py-2 bg-purple-600 rounded-lg hover:bg-purple-700"
          >
            View Leaderboard üèÜ
          </a>
        </div>
      )}
    </div>
  );
}
